
import "./navbarHeader.css";
import React, { useEffect, useState, useRef, useContext } from "react";
import { useParams } from "react-router-dom";
import { Navigate } from "react-router-dom";
import { collapseMenu } from "./OverlayMenu";
import VersesContext from "../contexts/BibleContext";

import { addBookmark } from "./AddBookmark";

import { getBook, getBookChapterNums, books } from "../Utilities";
import { Swiper, useSwiper, SwiperSlide } from "swiper/react";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./swiper_styles.css";
import { Pagination, Navigation, A11y } from "swiper";

import loadable from "@loadable/component";
import { BookmarkIcon } from "./AppIcons";
import FooterBar from "./FooterBar";
const Chapter = loadable(() => import("./Chapter"));


function Book() {
    const { book, chapter, verse_id } = useParams();
    const [readWhat, setReadWhat] = useState({
        "book": book,
        "chapter": chapter || 1,
    });

    const verseId = verse_id || 0;

    const [chapterArray, setChapterArray] = useState([]);

    const [navbarVisible, setNavbarVisible] = useState(true);

    const hasChapters = useRef(false);

    const { handleReadPosition } = useContext(VersesContext);

    const [bookmarkId, setBookmarkId] = useState(0);

    document.getElementById("menu-overlay").style.visibility = "collapse";
    
    const SwiperPosition = () => {
        const swiper = useSwiper();

        // Get from the Bible only selected book (and the chapter)
        useEffect(() => {
            let mounted = true;
            
            if (!hasChapters.current) {

                if (mounted) {
                    setChapterArray(getBookChapterNums(readWhat.book));
                }

                //Move slide
                const index = Number(readWhat.chapter) - 1;
                swiper.slideTo(index);

                //Set readPosition
                handleReadPosition(`/verse/${readWhat.book}/${readWhat.chapter}/0`);
            }
            hasChapters.current = true;
            return () => mounted = false;
        }, [swiper])

        return (<></>)
    }

    function handleChange(event) {
        const { name, value } = event.target;

        setReadWhat(prevRead => {
            if (name === "book") {
                return { "book": value, "chapter": 1 };
            }
            else {
                return {
                    ...prevRead,
                    [name]: value
                };
            }
        });
        hasChapters.current = false;
    }

    function handleSwipe(chptr) {
        setReadWhat(prevVal => {
            return { ...prevVal, chapter: chptr }
        });
        hasChapters.current = false;
    }

    function bookmarkThis() {
        if (window.confirm(`Postavi knjižnu oznaku za ${getBook(readWhat.book).name} ${readWhat.chapter}`)) {
            addBookmark(readWhat.book, readWhat.chapter)
                .then(bmId => setBookmarkId(bmId))
                .catch(setBookmarkId(0));
        };
    }

    useEffect(() => {
        const handleScroll = event => {
            if (window.scrollY < 110) {
                setNavbarVisible(true);
            } else if (window.scrollY < 140) {
                setNavbarVisible((prevVal) => setNavbarVisible(prevVal))
            } else {
                setNavbarVisible(false);
            }
        };
    
        window.addEventListener('scroll', handleScroll);
    
        return () => {
          window.removeEventListener('scroll', handleScroll);
        };
      }, []);

    return (
        <div name="swiper-page">
            {
                bookmarkId !== 0 ? <Navigate to={`/bookmark/${bookmarkId}`} /> :
                    <div>
                        <div className="navbar" style={
                            navbarVisible ? { "visibility": "visible" } : { "visibility": "collapse" }
                        }>
                            <div className="nav-item">
                                <select id="book-select" name="book" value={readWhat.book} onChange={handleChange}>
                                    {books.map(
                                        book => <option key={book.eng_short} value={book.eng_short}>{book.name}</option>
                                    )
                                    }
                                </select>

                                <select id="chapter-select" name="chapter" value={readWhat.chapter} onChange={handleChange}>
                                    {chapterArray.map(
                                        chapter => <option key={chapter} value={chapter}>{chapter}</option>
                                    )
                                    }
                                </select>
                            </div>

                            <div className="nav-item bookmark-button" onClick={() => bookmarkThis()}>
                                <BookmarkIcon />
                            </div>
                        </div>

                        {navbarVisible ? <div style={{ "height": "3rem" }}></div> : <></>}

                        <Swiper
                            pagination={{
                                type: "fraction",
                            }}
                            navigation={true}
                            modules={[Pagination, Navigation, A11y]}
                            onSlideChange={(swiper) => {
                                handleSwipe(swiper.activeIndex + 1);
                                window.scrollTo(0, 0);
                            }}
                            onClick={collapseMenu}
                            className="mySwiper"
                        >
                            <SwiperPosition />
                            {
                                chapterArray.map((chptr) => {

                                    return (
                                        <SwiperSlide key={chptr}>
                                            <Chapter
                                                bookName={getBook(readWhat.book).name}
                                                bookEngShort={readWhat.book}
                                                chapter={readWhat.chapter}
                                                markedVerse={Number(verseId)}
                                            />
                                        </SwiperSlide>
                                    )
                                })
                            }
                        </Swiper>
                        <FooterBar visible={navbarVisible} />
                    </div>
            }
        </div>
    )
}

export default Book;