import { db } from "../db";


export async function addBookmark(book, chapter) {
    const remark = "Knjižna oznaka";
    let datetime = (new Date()).toLocaleString();
    //
    const theBookmark = await db.notes.where({book: book, chapter: chapter}).first();
    if (typeof theBookmark === 'undefined') {
        try {
            const newRecordId = await db.notes.add({
                book,
                chapter,
                datetime,
                remark
            });
            return newRecordId;

        } catch (error) {
            return 0; // Add failed!
        }
    } else {
        return theBookmark.id;
    }
    //  
}